* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}

body {
  font-family: montserrat;
}

.code-nav nav {
  background: #011727;
  height: 80px;
  width: 100%;
}

.code-nav label.logo {
  color: white;
  font-size: 35px;
  line-height: 80px;
  padding: 0 100px;
  font-weight: bold;
  /* margin-top: -13px; */

}

.code-nav nav ul {
  float: right;
  margin-right: 20px;
  /* margin-top: -13px; */
}

.code-nav nav ul li {
  display: inline-block;
  line-height: 80px;
  margin: 0 5px;
}

.code-nav nav ul li a {
  color: white;
  font-size: 17px;
  padding: 10px 15px;
  border-radius: 3px;
  text-transform: uppercase;

}

.code-nav a:hover {
  background: #1b9bff;
  transition: .5s;
  text-decoration: none;
}

.code-nav .checkbtn {
  font-size: 30px;
  color: white;
  float: right;
  line-height: 80px;
  margin-right: 40px;
  cursor: pointer;
  display: none;
}

.code-nav #check {
  display: none;
}

@media (max-width: 952px) {
  .code-nav label.logo {
    font-size: 30px;
    padding-left: 50px;
  }

  .code-nav nav ul li a {
    font-size: 16px;
  }
}

@media (max-width: 858px) {
  .code-nav .checkbtn {
    display: block;
    /* margin-top: -13px; */
  }

  .code-nav ul {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: #04292e;
    top: 80px;
    left: -100%;
    z-index: 9999;
    text-align: center;
    transition: all .5s;
  }

  .code-nav nav ul li {
    display: block;
    margin: 50px 0;
    line-height: 30px;
  }

  .code-nav nav ul li a {
    font-size: 20px;
  }

  .code-nav #check:checked~ul {
    left: 0;
  }
}

.cart-box span {

  width: 20px !important;
  height: 20px !important;
  background-color: coral;
  padding: 5px;
  border-radius: 15px;

}

.logo a {
  color: #fff !important;
}

.logo a:hover {
  background-color: transparent !important;
  color: #fff !important;
}